.App {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: rgb(123, 123, 123); */
  background-color: lightslategray;
  height: 100vh;
  width: 100vw;
  margin: auto;}
.login-page {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: rgb(123, 123, 123); */
  background-color: lightslategray;
  height: 100vh;
  width: 100vw;
  margin: auto;}
.login{
  font-weight: bold;
  font-size: 26px;
  color: rgb(215, 129, 21);
  font-family: Verdana, Geneva, Tahoma, sans-serif;}
.login-panel{
  background: whitesmoke;
  display:flex;
  flex-direction: row;
  height: 50%;
  width: 50%;
  border-radius: 8px;
  /* box-shadow: 10px 10px 20px rgb(215, 129, 21); */
}
.login-logo{
  background-color: rgb(145, 122, 94);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  width: 50%;
  height: 100%;}
.login-options{
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    background-color: rgb(240, 232, 222);
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.username-section{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15%;
  
}
.password-section{
  /* padding-top: 10px; */
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15%;
}
.button-section{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15%;
  /* padding-top: 30px; */
}
.login-field{
  border-bottom:rgb(145, 122, 94);
  border-bottom-width: thin;
  border-bottom-style: solid;
  border-top: none;
  border-left: none;
  border-right: none;
  text-align: center;
  border-radius: 8px;
  width: 55%;
  height: 70%;
  font-size: 10pt;
  font-weight: bolder;}
.login-button{
  width: 40%;
  height: 60%;
  font-size: 10pt;
  border-radius: 8px;
  border-style: none;
  color: white;
  font-weight: bolder;
  background-color: #04203A;
  opacity: 1;
  transition: 10ms;}
.login-button:hover{
  opacity: 0.6;}
.account-options{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 15%;
}
.sign-up-label{
margin-right: 2em;
font-size: 11px;}
.forgot-password-label{
  font-size: 11px;
  margin-left: 2em;}
.system-name{
  color: #04203A;
  text-align: center;
}
.home-plate{
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  background-color: #20232a;
  /* box-shadow: 10px 10px 20px rgb(70, 70, 70); */
  /* border-radius: 5px; */
  /* overflow-y: auto; */}

.system-title{
  display: flex;
  justify-content: center;
  align-content: center;
  color: white;
  font-size: 30px;
  font-weight: bold;
  width: 100%;
  height: 10%;
  border-bottom: white;
  border-width: 1px; 
  border-bottom-style: solid; }
.logo-div{
  width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    height: 15%;
    border-top: white;
    border-top-width: 1px; 
    border-top-style: solid; }
.logo{
  height: 30%;
  width: 100;}
.boc-form-panel{
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: rgb(145, 122, 94);
  border-width: 1px; 
  border-radius: 5px;}
.divider2{
    height: 75%;}
.margin{
  height: 2%;
}
.options-panel{
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-style: none;
  /* border-top-left-radius: 8px;
  border-top-right-radius: 8px; */
  height: 12%;
  width: 100%;
  background-color: rgb(145, 122, 94) ;
  /* background-color: #1976d2; */
  border: none;}
.form-widgets{
  border-style: none;
  /* border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px; */
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  height: 88%;
  width: 100%;}
.body{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;}
.form-control-buttons{
    display: flex;
    flex-direction: row;
    height: 10%;
    width: 100%;}
.rest-of-the-form{
height: 90%;
display: flex;
overflow-y: auto;
flex-direction: column;
width: 100%;}
.new-bill-icon{
  transition: all .3s ease-in-out;
  /* border: white; */
  background-image: url('/public/newbill.png');
  /* border-style: solid;
  border-width: 1px;
  border-radius: 10px; */
  height: 60%;
  width: 100%;}
.new-bill-icon:hover{
    /* -webkit-box-shadow: 0 0 8px #fff;
    box-shadow: 0 0 8px #fff; */
    cursor: grabbing;
    opacity: 0.6;
    transform: scale(1.4);}
.draft-icon{
  transition: all .3s ease-in-out;
  height: 60%;
  width: 100%;
  /* border: white;
  border-style: solid;
  border-radius: 10px;
  border-width: 1px; */}
.draft-icon:hover{
    /* -webkit-box-shadow: 0 0 8px #fff;
    box-shadow: 0 0 8px #fff; */
    cursor: grabbing;
    opacity: 0.6;
    transform: scale(1.4);}
.archive-icon{
  transition: all .3s ease-in-out;
  height: 60%;
  width: 100%;}
.archive-icon:hover{
    /* -webkit-box-shadow: 0 0 8px #fff;
    box-shadow: 0 0 8px #fff; */
    opacity: 0.6;
    cursor: grabbing;
    transform: scale(1.4);}
.profile-icon:hover{
    /* -webkit-box-shadow: 0 0 8px #fff;
    box-shadow: 0 0 8px #fff; */
    cursor: grabbing;
    transform: scale(1.4);}
.new-bill-label, .draft-label, .archive-label, .profile-label{
  display: flex;
  justify-content: center;
  align-content: center;
  font-size: 15px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  /* font-weight: bold; */
  color: white;}
.profile-icon{
    height: 60%;
    transition: all .3s ease-in-out; 
    width: 100%;}

.input-field{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.disbursementDetails{
  display: flex;
  flex-direction: column;
  height: 200px;
  width: 300px;
}
#disbursementDescription{
  height: 80px;
}
.disbursementField{
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  border-color: #04203A;
  font-family: inherit;
}